// RESET CSS
// ressとnormalizeを元に無駄なスタイルを全消ししたい
// 記述されていない要素の振る舞いで困ったら各自適当に足してください。

// 全てのmargin、paddingは削除
// そしてborder-boxへ
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  // -webkit-appearance: none
 }  // appearance: none

// 新要素のblock達
article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block; }

// fontサイズ系の振る舞いをリセット
h1,
h2,
h3,
h4,
h5,
h6,
small {
  font-size: 100%;
  font-weight: inherit; }

th,
dt,
optgroup {
  font-weight: normal; }

strong,
b {
  font-weight: bold; }

// なんかie10で背景がグレーになるのを防ぐのと、
// iOS8+ safari8+で下に空白出るのを削除
a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
  // リンクのhover、active時のアウトラインを消す
  &:active,
  &:hover {
    outline: none; } }

img {
  border-style: none; }

// ie11以下のため
svg:not(:root) {
  overflow: hidden; }

// ie11以下のため
audio,
canvas,
progress,
video {
  display: inline-block; }

// table
table {
  border-collapse: collapse;
  border-spacing: 0; }

// フォーム系の振る舞い
button,
input,
select,
textarea {
  font-size: 100%;
  border: none;
  background-color: rgba(0,0,0,0);
  outline: none;
  color: inherit; }

button,
[type="reset"],
[type="submit"],
[role="button"] {
  cursor: pointer; }

[disabled] {
  cursor: default; }

[type="range"],
[type="checkbox"],
[type="file"] {
  -webkit-appearance: none; }


// Safari 8+
[type="search"] {
  -webkit-appearance: none; }

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: none; }

// iOS and Safari
::-webkit-file-upload-button {
  -webkit-appearance: none;
  font: inherit; }

// iOS and Safari chrome
[type="search"] {
  -webkit-appearance: none;
  outline-offset: -2px; }


// 以下ressを踏襲

// FireFox36+
[type="number"] {
  width: auto; }

// Safari 8+
[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

// ie11+
textarea {
  overflow: auto;
  resize: vertical; }

// Firefox 4+
button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: 0;
  padding: 0; }

button:-moz-focusring,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  outline: none; }


// Firefox 40+, Internet Explorer 11-
button,
select {
  text-transform: none; }

// Firefox 36+ Chrome 41+
select {
  -moz-appearance: none;
  -webkit-appearance: none; }

// Internet Explorer 11+
select::-ms-expand {
  display: none; }

// Internet Explorer 11+
select::-ms-value {
  color: currentColor; }

// Internet Explorer 11+
legend {
  border: 0;
  color: inherit;
  display: table;
  max-width: 100%;
  white-space: normal; }
