@charset "UTF-8";
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block; }

h1,
h2,
h3,
h4,
h5,
h6,
small {
  font-size: 100%;
  font-weight: inherit; }

th,
dt,
optgroup {
  font-weight: normal; }

strong,
b {
  font-weight: bold; }

a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects; }
  a:active, a:hover {
    outline: none; }

img {
  border-style: none; }

svg:not(:root) {
  overflow: hidden; }

audio,
canvas,
progress,
video {
  display: inline-block; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

button,
input,
select,
textarea {
  font-size: 100%;
  border: none;
  background-color: rgba(0, 0, 0, 0);
  outline: none;
  color: inherit; }

button,
[type="reset"],
[type="submit"],
[role="button"] {
  cursor: pointer; }

[disabled] {
  cursor: default; }

[type="range"],
[type="checkbox"],
[type="file"] {
  -webkit-appearance: none; }

[type="search"] {
  -webkit-appearance: none; }

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: none; }

::-webkit-file-upload-button {
  -webkit-appearance: none;
  font: inherit; }

[type="search"] {
  -webkit-appearance: none;
  outline-offset: -2px; }

[type="number"] {
  width: auto; }

[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

textarea {
  overflow: auto;
  resize: vertical; }

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: 0;
  padding: 0; }

button:-moz-focusring,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  outline: none; }

button,
select {
  text-transform: none; }

select {
  -moz-appearance: none;
  -webkit-appearance: none; }

select::-ms-expand {
  display: none; }

select::-ms-value {
  color: currentColor; }

legend {
  border: 0;
  color: inherit;
  display: table;
  max-width: 100%;
  white-space: normal; }

html {
  font-size: 62.5%;
  background-color: #fff; }

body {
  width: 100%;
  color: #000;
  text-align: left;
  font-size: 1.4rem;
  font-weight: normal;
  font-family: verdana, -apple-system, "Hiragino Kaku Gothic ProN", "ヒラギノ角ゴ ProN W3", Meiryo, メイリオ, sans-serif;
  line-height: 1;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  background-color: #fff;
  overflow-x: hidden;
  min-height: 100vh; }

a {
  color: #000;
  transition: opacity 0.4s;
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }
  a:hover {
    opacity: 0.6; }

img {
  max-width: 100%;
  vertical-align: bottom; }

ul {
  list-style-type: none;
  padding: 0;
  margin: 0; }

ul ul {
  margin-left: 0; }

dt {
  font-weight: bold; }

dd {
  margin-left: 0; }

ol {
  list-style: none;
  list-style-position: inside; }

small, span {
  vertical-align: baseline; }

@media screen and (max-width: 47.9375em) {
  .sp-displayNone {
    display: none; } }

@media screen and (max-width: 64em) {
  .sp-tab-displayNone {
    display: none; } }

@media screen and (min-width: 48em) {
  .tab-pc-displayNone {
    display: none; } }

@media screen and (min-width: 64.001em) {
  .pc-displayNone {
    display: none; } }

@media screen and (min-width: 48em) {
  .hide-pc {
    display: none; } }

@media screen and (max-width: 47.9375em) {
  .hide-sp {
    display: none; } }
